.features__link {

  grid-column: 1 / 3;
  grid-row: 3 / 4;

  font-family: "Oswald", sans-serif;
  font-size: 16px;
  text-transform: uppercase;
  line-height: 16px;
  color: #000000;
  padding-left: 19px;
}
